<template>
  <div class="list">
    <div
      class="pageBanner"
      :style="'background-image:url(' + pageBanner + ');'"
    ></div>
    <div class="pageNav">
      <h3>
        <span>{{ pageNavList.Name }}</span>
      </h3>
      <dl>
        <dt>当前位置：</dt>
        <dd>{{ pageNavList.Name }}</dd>
        <template v-if="pageNav.Name">
          <dd>{{ pageNav.Name }}</dd>
        </template>
      </dl>
    </div>
    <div class="pageMain clearfix">
      <router-view :key="$route.fullPath" />
    </div>
  </div>
</template>

<script>
export default {
  name: "list",
  data() {
    return {
      msg: "yemian",
      pageCid: "",
      pageNav: [],
      pageFCid: "",
      pageNavList: [],
      pageRouterList: [],
      pageBanner: require("../assets/img/pageBanner.jpg"),
    };
  },
  mounted() {
    var that = this;
    that.pageRouterList = that.$route.query;
    if (that.pageRouterList.cid) {
      that.pageCid = that.pageRouterList.cid;
      that.navFindF();
    } else {
      that.pageCid = that.Base.GetNewsInfoByNid(that.pageRouterList.id).Fid;
      that.navFindF();
    }
    if (!(that.pageNavList.Logo == "http://base.hope55.com")) {
      if (that.pageCid == that.api.zysz) {
        that.pageBanner = require("../assets/img/zysz_pagebanner.jpg");
      } else {
        that.pageBanner = that.pageNavList.Logo;
      }
    }
  },
  methods: {
    Navformat: function (data) {
      var obj = data;
      // console.log(obj);
      var temp = [];
      for (var i = 0; i < obj.length; i++) {
        if (obj[i].FID == "0") {
          var main = obj[i];
          main.Clist = [];
          for (var j = 0; j < obj.length; j++) {
            if (obj[j].FID == main.ID) {
              main.Clist.push(obj[j]);
            }
          }
          temp.push(main);
        }
      }
      return temp;
    },
    navFindF: function (id) {
      var that = this;
      var navList = that.Navformat(that.Base.GetNavColumnsByDid());
      // console.log(that.pageCid);
      // console.log(that.Base.GetColumnByid(that.pageCid));
      if (that.Base.GetColumnByid(that.pageCid).Fid) {
        that.pageNav = that.Base.GetColumnByid(that.pageCid);
        that.pageFCid = that.Base.GetColumnByid(that.pageCid).Fid;
        for (let key in navList) {
          if (navList[key].ID == that.pageFCid) {
            that.pageNavList = navList[key];
          }
        }
      } else {
        for (let key in navList) {
          if (navList[key].ID == that.pageCid) {
            that.pageNavList = navList[key];
          }
        }
      }
      // console.log(that.pageNavList);

        // console.log(that.pageCid);
      if (that.pageNavList.length == 0) {
        // console.log(that.pageFCid);
        that.pageNavList = that.Base.GetColumnByid(that.pageFCid?that.pageFCid:that.pageCid);
      }
      // console.log(typeof that.pageNavList.ID == 'number');
    },
  },
};
</script>

<style scoped>
.list {
  width: 100%;
  overflow: hidden;
  padding-bottom: 40px;
}
.pageBanner {
  width: 100%;
  height: 300px;
  background-position: center;
  background-repeat: no-repeat;
  background-size: auto 100%;
}
.pageNav {
  width: 690px;
  height: 50px;
  background-color: #f7f7fa;
  margin: 0 auto;
}
.pageNav h3 {
  float: left;
  width: 260px;
  height: 50px;
  line-height: 50px;
  text-indent: 82px;
  font-size: 24px;
  color: white;
  position: relative;
}
.pageNav h3::before {
  content: "";
  width: 220px;
  height: 70px;
  bottom: 0px;
  left: 0px;
  position: absolute;
  background-color: #4273ff;
}
.pageNav h3::after {
  content: "";
  width: 0px;
  height: 0px;
  bottom: 0px;
  right: 0px;
  position: absolute;
  border-left: solid 20px #4273ff;
  border-bottom: solid 35px #4273ff;
  border-right: solid 20px transparent;
  border-top: solid 35px transparent;
}
.pageNav h3 span {
  display: block;
  width: 100%;
  height: 100%;
  position: absolute;
  left: 0px;
  top: 0px;
  z-index: 3;
}
.pageNav dl {
  float: right;
}
.pageNav dl > * {
  height: 50px;
  display: block;
  float: left;
  line-height: 50px;
  font-size: 20px;
  color: #666;
  letter-spacing: 1px;
}
.pageNav dl dd::after {
  content: "·";
  margin: 0 5px;
}
.pageNav dl dd:last-child {
  color: #4a77f6;
}
.pageNav dl dd:last-child::after {
  display: none;
}
.pageMain {
  width: 100%;
  margin: 0 auto;
  margin-top: 50px;
  padding-top: 1px;
}
</style>



