<template>
  <div class="imgfontlist">
    <div class="pageCont">
      <ul>
        <li v-for="i in pageList" :key="i.ID">
          <router-link :to="{path:'/list/content',query:{id:i.ID}}">
            <div class="imgBox" :style="'background-image: url('+i.Cover+');'"></div>
            <div class="font">
              <h3>
                <span>{{ i.Title }}</span>
                <i>
                  <b></b>
                  <em>{{ Curren.formatDatebox(i.ShowTime) }}</em>
                </i>
              </h3>
              <p>{{ i.Describe }}</p>
            </div>
          </router-link>
        </li>
      </ul>
      <pageMove @pageindex="pageF" :pageOption="pageOption"></pageMove>
    </div>
    <hotzy></hotzy>
  </div>
</template>

<script>
export default {
  name: "imgfontlist",
  data() {
    return {
      pathList: [],
      pageList: [],
      pageOption: {
        pageIndex: 1,
        pageTotal: "",
        pageSize: 7
      }
    };
  },
  created() {
    this.pathList = this.$route.query;
    this.getPageList();
  },
  mounted() {},
  methods: {
    pageF(data) {
      this.pageOption.pageIndex = data;
      this.getPageList();
    },
    getPageList() {
      var temp = this.Base.GetNewsInfoByCid(
        "",
        this.pathList.cid,
        "",
        "",
        "",
        this.pageOption.pageIndex,
        "",
        this.pageOption.pageSize
      );
      this.pageList = temp.row;
      this.pageOption.pageTotal = temp.total;
      // console.log(temp)
    }
  }
};
</script>

<style scoped>
.pageCont {
  width: 100%;
}
ul {
  width: 690px;
  margin: 0 auto;
  padding-bottom: 35px;
}
li {
  height: 117px;
  background-color: #f3f3f3;
  margin-bottom: 29px;
  transition: box-shadow 0.4s;
}
li:last-child {
  margin-bottom: 0px;
}
li .imgBox {
  width: 189px;
  height: 117px;
  float: left;
  overflow: hidden;
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
}
li .font {
  width: 474px;
  float: right;
  margin-right: 5px;
}
li .font h3 {
  height: 24px;
  line-height: 24px;
  margin-top: 15px;
}
li .font h3 span {
  display: block;
  font-size: 20px;
  color: #333333;
  height: 24px;
  transition: all 0.4s;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  max-width: 300px;
  float: left;
}
li .font h3 i {
  display: block;
  float: right;
  font-size: 18px;
  color: #666666;
  position: relative;
}
li .font h3 i::before {
  content: "";
  width: 16px;
  height: 16px;
  background: url(../assets/img/time_icon.png) center no-repeat;
  background-size: 100%;
  position: absolute;
  top: 4px;
  left: -20px;
}
li .font p {
  height: 52px;
  text-align: justify;
  margin-top: 15px;
  font-size: 20px;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 2;
  overflow: hidden;
  color: #999999;
  line-height: 26px;
}
li:hover {
  box-shadow: 0px 0px 16px 0px rgba(43, 60, 198, 0.2);
}
li:hover .font h3 span {
  color: #4273ff;
}
</style>



