<template>
  <div class="pagecontent">
    <div class="pageCont">
      <div class="content-cont">
        <div class="content-cont-title">{{ pageList.Title }}</div>
        <div class="content-cont-ftitle">
          <span>
            <i class="icon-time"></i>
            <em>{{ pageList.ShowTime }}</em>
          </span>
          <span>
            <i class="icon-view"></i>
            <em>{{ pageList.Press }}</em>
          </span>
        </div>
        <!-- {{pageList.Details}} -->
        <div class="content-cont-html" v-html="pageList.Details"></div>
      </div>
    </div>
    <div class="pageHotZy">
      <hotzy></hotzy>
    </div>
  </div>
</template>

<script>
export default {
  name: "pagecontent",
  data() {
    return {
      routerList: [],
      pageList: []
    };
  },
  mounted() {
    var that = this;
    that.routerList = that.$route.query;
    that.pageList = that.Base.GetNewsInfoByNid(that.routerList.id);
    // console.log(that.pageList)
  },
  methods: {}
};
</script>

<style scoped>
.pageCont {
  width: 690px;
  margin: 0 auto;
  margin-bottom: 30px;
}
.content-cont {
  width: 100%;
  padding: 30px 30px;
  box-sizing: border-box;
  background-color: #ffffff;
  box-shadow: 0px 0px 16px 0px rgba(43, 60, 198, 0.2);
}
.content-cont .content-cont-title {
  text-align: center;
  font-size: 20px;
  line-height: 30px;
  font-weight: bold;
  padding-bottom: 15px;
  border-bottom: solid 1px #eeeeee;
}
.content-cont-ftitle {
  text-align: center;
  font-size: 14px;
  color: #666666;
  line-height: 18px;
  height: 18px;
  margin-top: 20px;
  margin-bottom: 20px;
}
.content-cont-ftitle span {
  margin: 0px 15px;
  display: inline-block;
}
.content-cont-ftitle span i {
  margin-right: 5px;
  display: block;
  float: left;
}
.content-cont-ftitle span em {
  display: block;
  float: left;
  line-height: 20px;
}
.content-cont-ftitle span i.icon-time {
  width: 18px;
  height: 18px;
  background: url(../assets/img/time_icon.png) center no-repeat;
  background-size: 100% 100%;
}
.content-cont-ftitle span i.icon-view {
  width: 20px;
  height: 12px;
  background: url(../assets/img/yanjing_icon.png) center no-repeat;
  background-size: 100% 100%;
  margin-top: 3px;
}
.content-cont-html {
  text-align: justify;
  text-indent: 2em;
  font-size: 20px;
  line-height: 1.75;
}
.content-cont-html >>> * {
  font-size: 20px !important;
  line-height: 1.75 !important;
  font-family: "simhei" !important;
}
.content-cont-html >>> img {
  display: block;
  margin: 0 auto;
  max-width: 100%;
  height: auto;
}
</style>



