<template>
  <div class="danye">
    <div class="info" v-html="thatPageList.Info"></div>
    <hotzy></hotzy>
  </div>
</template>

<script>
export default {
  name: "danye",
  data() {
    return {
      thatRouterList: [],
      thatPageList: [],
    };
  },
  mounted() {
    var that = this;
    that.thatRouterList = that.$route.query;
    that.thatPageList = that.Base.GetSinglePageInfoByCid(
      "",
      that.thatRouterList.cid
    );
    console.log(that.thatPageList);
  },
  methods: {},
};
</script>

<style scoped>
.danye .info {
  width: 6.9rem;
  margin: 0 auto;
  font-size: 0.2rem;
  text-align: justify;
  padding-bottom: 10px;
}
.danye .info>>>img {
  display: block;
  max-width: 100%;
  height: auto;
}
</style>



