<template>
  <div class="imglist">
    <ul class="clearfix">
      <li v-for="i in thatPageList" :key="i.ID">
        <div class="imgBox">
          <img class="T_imgItem" :src="i.FileUrl" alt="" />
        </div>
        <p>{{ i.Title }}</p>
      </li>
    </ul>
    <pageMove @pageindex="pageF" :pageOption="pageOption"></pageMove>
    <hotzy></hotzy>
  </div>
</template>

<script>
export default {
  name: "imglist",
  data() {
    return {
      thatRouterList: [],
      thatPageList: [],
      pageOption: {
        pageIndex: 1,
        pageTotal: "",
        pageSize: 8,
      },
    };
  },
  created() {
    var that = this;
    that.thatRouterList = that.$route.query;
    this.getPageList();
  },
  mounted() {},
  methods: {
    pageF(data) {
      this.pageOption.pageIndex = data;
      this.getPageList();
    },
    getPageList() {
      var temp = this.Base.GetFilesInfoByCid(
        "",
        this.thatRouterList.cid,
        "",
        "",
        this.pageOption.pageIndex,
        "",
        this.pageOption.pageSize
      );
      this.thatPageList = temp.GetData;
      this.pageOption.pageTotal = temp.Total;
    },
  },
};
</script>

<style scoped>
.imglist ul {
  width: 6.9rem;
  margin: 0 auto;
  margin-bottom: 30px;
}
.imglist ul li {
  display: block;
  width: 3.3rem;
  background-color: #ffffff;
  box-shadow: 0rem 0.04rem 0.07rem 0rem rgba(0, 0, 0, 0.05);
  float: left;
  margin-top: 0.3rem;
}
.imglist ul li:nth-child(-n + 2) {
  margin-top: 0rem;
}
.imglist ul li:nth-child(even) {
  float: right;
}
.imglist ul li .imgBox {
  width: 3.3rem;
  height: 2.2rem;
}
.imglist ul li p {
  font-size: 0.24rem;
  font-weight: normal;
  font-stretch: normal;
  letter-spacing: 0rem;
  color: #333333;
  text-align: center;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  line-height: 46px;
}
</style>



