<template>
  <div class="mlxy">
    <div class="mlxy-row xyfg">
      <div class="mlxy-head">校园风光</div>
      <swiper :options="swiperOption" class="swiper-container">
        <!-- slides -->
        <swiper-slide
          class="swiper-item"
          v-for="i in mlxyList.xyshList"
          :key="i.ID"
        >
          <img :src="i.FileUrl" alt />
        </swiper-slide>
        <!-- Optional controls ,显示小点-->
        <div class="swiper-pagination" slot="pagination"></div>
      </swiper>
    </div>
    <div class="mlxy-row sxsb">
      <div class="mlxy-head">实训设备</div>
      <swiper :options="swiperOption1" class="swiper-container">
        <!-- slides -->
        <swiper-slide
          class="swiper-item"
          v-for="i in mlxyList.sxsbList"
          :key="i.ID"
        >
          <img :src="i.FileUrl" alt />
        </swiper-slide>
        <!-- Optional controls ,显示小点-->
        <div class="swiper-pagination" slot="pagination"></div>
      </swiper>
    </div>
    <div class="mlxy-row xysh">
      <div class="mlxy-head">校园生活</div>
      <swiper :options="swiperOption2" class="swiper-container">
        <!-- slides -->
        <swiper-slide
          class="swiper-item"
          v-for="i in mlxyList.xyshList"
          :key="i.ID"
        >
          <img :src="i.FileUrl" alt />
        </swiper-slide>
        <!-- Optional controls ,显示小点-->
        <div class="swiper-pagination" slot="pagination"></div>
      </swiper>
    </div>
    <hotzy></hotzy>
  </div>
</template>

<script>
import { swiper, swiperSlide } from "vue-awesome-swiper";

export default {
  name: "mlxy",
  data() {
    return {
      swiperOption: {
        // 参数选项,显示小点
        pagination: ".xyfg .swiper-pagination",
        //循环
        loop: true,
        //每张播放时长3秒，自动播放
        autoplay: 3000,
        grabCursor: true,
        //滑动速度
        speed: 500,
        // delay:1000,
        //设置点击箭头
        prevButton: ".xyfg .swiper-button-prev",
        nextButton: ".xyfg .swiper-button-next",
      },
      swiperOption1: {
        // 参数选项,显示小点
        pagination: ".sxsb .swiper-pagination",
        //循环
        loop: true,
        //每张播放时长3秒，自动播放
        autoplay: 3000,
        grabCursor: true,
        //滑动速度
        speed: 500,
        // delay:1000,
        //设置点击箭头
        prevButton: ".sxsb .swiper-button-prev",
        nextButton: ".sxsb .swiper-button-next",
      },
      swiperOption2: {
        // 参数选项,显示小点
        pagination: ".xysh .swiper-pagination",
        //循环
        loop: true,
        //每张播放时长3秒，自动播放
        autoplay: 3000,
        grabCursor: true,
        //滑动速度
        speed: 500,
        // delay:1000,
        //设置点击箭头
        prevButton: ".xysh .swiper-button-prev",
        nextButton: ".xysh .swiper-button-next",
      },
      mlxyList: {
        xyfgList: [],
        sxsbList: [],
        xyshList: [],
      },
    };
  },
  mounted() {
    var that = this;
    that.mlxyList.xyfgList = that.Base.GetFilesInfoByCid(
      "",
      that.api.xyfg,
      "",
      "",
      1,
      "",
      10
    ).GetData;
    that.mlxyList.sxsbList = that.Base.GetFilesInfoByCid(
      "",
      that.api.sxsb,
      "",
      "",
      1,
      "",
      10
    ).GetData;
    that.mlxyList.xyshList = that.Base.GetFilesInfoByCid(
      "",
      that.api.xysh,
      "",
      "",
      1,
      "",
      10
    ).GetData;
  },
  methods: {},
  components: {
    swiper,
    swiperSlide,
  },
};
</script>

<style scoped>
.mlxy {
  padding-bottom: 10px;
}
.mlxy-row {
  width: 690px;
  margin: 0 auto;
  margin-bottom: 50px;
  position: relative;
}
.mlxy-row:last-child {
  margin-bottom: 0px;
}
.mlxy-head {
  height: 50px;
  background: url(../assets/img/mlxy_titleBG.png) center no-repeat;
  font-size: 24px;
  line-height: 50px;
  text-align: center;
  color: white;
  margin-bottom: 34px;
}
.mlxy-row >>> .swiper-container {
  width: 690px;
  height: 477px;
}
.mlxy-row >>> .swiper-container img {
  width: 100%;
  height: 100%;
}
.mlxy-row >>> .swiper-pagination-bullet {
  width: 6px;
  height: 6px;
  opacity: 0.5;
  border-radius: 3px;
  background-color: #ffffff;
  transition: width 0.4s;
}
.mlxy-row >>> .swiper-pagination-bullet-active {
  width: 22px;
  opacity: 0.8;
}
</style>



