<template>
  <div class="bxzq">
    <div class="sxwy">
      <ul class="clearfix">
        <template v-for="i of xcpList">
          <li :key="i.ID">
            <router-link :to="{ path: '/list/content', query: { id: i.ID } }">
              <div class="imgBox">
                <img :src="i.Cover" alt />
              </div>
              <p>
                <b>{{ i.Title }}</b>
                <span>初中起点</span>
              </p>
            </router-link >
          </li></template
        >
      </ul>
    </div>
    <hotzy></hotzy>
  </div>
</template>

<script>
export default {
  name: "bxzq",
  data() {
    return {
      pageNavList: [],
      xcpList: [],
      pageRouterList: [],
    };
  },
  mounted() {
    var that = this;
    that.pageRouterList = that.$route.query;
    that.pageNavList = that.Base.GetColumnByid(that.pageRouterList.cid);
    that.xcpList = that.Base.GetNewsInfoByCid("", that.pageRouterList.cid).row;
    console.log(that.xcpList);
  },
  methods: {},
};
</script>

<style scoped>
.sxwy {
  padding: 0px 30px;
}
.sxwy li {
  width: 335px;
  height: 150px;
  float: left;
  box-shadow: 2px 24px 54px 0px rgba(43, 60, 198, 0.1);
  border-radius: 10px;
  margin-bottom: 30px;
}
.sxwy li:nth-child(even) {
  float: right;
}
.sxwy li a {
  overflow: hidden;
}
.sxwy li a .imgBox {
  width: 118px;
  height: 118px;
  border-radius: 59px;
  float: left;
  margin-left: 21px;
  margin-top: 15px;
  margin-right: 24px;
  overflow: hidden;
}
.sxwy li a p {
  float: left;
  width: 155px;
  color: white;
}
.sxwy li a p b {
  display: flex;
  height: 84px;
  font-size: 30px;
  line-height: 1.25;
  align-items: center;
  text-shadow: 0px 3px 7px 0px rgba(42, 67, 190, 0.2);
}
.sxwy li a p span {
  display: block;
  padding-left: 41px;
  position: relative;
  font-size: 22px;
  line-height: 33px;
  margin-top: 10px;
}
.sxwy li a p span::before {
  content: "";
  width: 30px;
  height: 33px;
  background: url(../assets/img/boshi_icon.png);
  background-size: 100%;
  position: absolute;
  left: 0px;
  top: 0px;
}
.sxwy li:nth-child(1) {
  background-image: linear-gradient(0deg, #4998ee 0%, #6faff3 100%);
}
.sxwy li:nth-child(2) {
  background-image: linear-gradient(0deg, #ffa03f 0%, #ffc15b 100%);
}
.sxwy li:nth-child(3) {
  background-image: linear-gradient(0deg, #845ef4 0%, #9686ec 100%);
}
.sxwy li:nth-child(4) {
  background-image: linear-gradient(0deg, #4998ee 0%, #6faff3 100%);
}
</style>



