<template>
  <div class="hdsg">
    <div class="xcpBox">
      <div
        class="xcpBanner"
        :style="'background-image: url(' + pageNavList.Logo + ');'"
      ></div>
      <div class="xcp-row">
        <div class="xcp-item" v-for="i in pageList" :key="i.Id">
          <div class="videoBox">
            <template v-if="!/www.cdwyh.com/.test(i.Url)">
              <iframe
                :src="i.Url"
                frameborder="0"
                width="100%"
                height="100%"
              ></iframe>
            </template>
            <template v-else>
              <video width="100%" height="100%" controls :poster="i.FileUrl" >
                <source :src="i.Url" type="video/mp4"/>
              </video>
            </template>
          </div>
          <p>{{ i.Title }}</p>
        </div>
      </div>
    </div>
    <pageMove @pageindex="pageF" :pageOption="pageOption"></pageMove>
    <hotzy></hotzy>
  </div>
</template>

<script>
export default {
  name: "hdsg",
  data() {
    return {
      pageNavList: [],
      pathList: [],
      pageList: [],
      pageOption: {
        pageIndex: 1,
        pageTotal: 0,
        pageSize: 6
      }
    };
  },
  mounted() {
    var that = this;
    that.pageNavList = that.Base.GetColumnByid(that.api.hdsg);
    that.pathList = that.$route.query;
    that.getPageList();

  },
  methods: {
    pageF(data) {
      this.pageOption.pageIndex = data;
      this.getPageList();
    },
    getPageList() {
      var temp = this.Base.GetFilesInfoByCid(
        "",
        this.pathList.cid,
        "",
        "",
        this.pageOption.pageIndex,
        "",
        this.pageOption.pageSize
      );
      this.pageList = temp.GetData;
      this.pageOption.pageTotal = temp.Total;
    },
  },
};
</script>

<style scoped>
.xcpBanner {
  width: 100%;
  height: 300px;
  background-position: center;
  background-repeat: no-repeat;
  background-size: 100% 100%;
}
.xcp-row{padding-bottom: 40px;}
.xcp-item {
  margin-top: 40px;
}
.xcp-item:first-child {
  margin-top: -54px;
}
.xcp-item .videoBox {
  width: 690px;
  height: 404px;
  margin: 0 auto;
  border-radius: 10px;
  overflow: hidden;
  margin-bottom: 35px;
}
.xcp-item .videoBox > * {
  display: block;
  width: 100%;
  height: 100%;
  border-radius: 10px;
  overflow: hidden;
}
.xcp-item > p {
  width: 690px;
  margin: 0 auto;
  font-weight: bold;
  font-size: 30px;
  line-height: 1.2;
  margin-top: 30px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
</style>



