<template>
  <div class="jnpx">
    <ul class="clearfix">
      <li v-for="i in pageList" :key="i.ID">
        <router-link :to="{path:'/list/content',query:{id:i.ID}}">
          <div class="imgBox" :style="'background-image: url('+i.Cover+');'"></div>
          <div class="font">
            <h3>{{ i.Title }}</h3>
            <p>{{ i.Describe }}</p>
          </div>
        </router-link>
      </li>
    </ul>
    <pageMove @pageindex="pageF" :pageOption="pageOption"></pageMove>
    <hotzy></hotzy>
  </div>
</template>

<script>
export default {
  name: "jnpx",
  data() {
    return {
      pageText: "",
      pageList: [],
      pageOption: {
        pageIndex: 1,
        pageTotal: 10,
        pageSize: 6
      }
    };
  },
  created() {
    this.pathList = this.$route.query;
    this.getPageList();
    // console.log(this.pageList)
  },
  methods: {
    pageF(data) {
      this.pageOption.pageIndex = data;
      this.getPageList();
    },
    getPageList() {
      var temp = this.Base.GetNewsInfoByCid(
        "",
        this.pathList.cid,
        "",
        "",
        "",
        this.pageOption.pageIndex,
        "",
        this.pageOption.pageSize
      );
      this.pageList = temp.row;
      this.pageOption.pageTotal = temp.total;
    }
  }
};
</script>

<style scoped>
ul {
  padding-bottom: 15px;
  width: 690px;
  margin: 0 auto;
}
li {
  width: 335px;
  height: 241px;
  position: relative;
  margin-bottom: 160px;
  float: left;
}
li:nth-child(even) {
  float: right;
}
li .imgBox {
  width: 100%;
  height: 100%;
  overflow: hidden;
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
}
li .font {
  width: 285px;
  height: 166px;
  position: absolute;
  bottom: -125px;
  left: 50%;
  transform: translateX(-50%);
  background-color: white;
  box-shadow: 0px 0px 16px 0px rgba(43, 60, 198, 0.2);
  transition: all 0.4s;
}
li .font h3 {
  width: 210px;
  height: 50px;
  background-color: #4273ff;
  border-radius: 10px;
  font-size: 24px;
  line-height: 50px;
  text-align: center;
  color: white;
  position: absolute;
  left: 50%;
  top: -25px;
  margin-left: -105px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  padding: 0px 10px;
}
li .font p {
  width: 95%;
  margin: 0 auto;
  text-align: justify;
  margin: 0 auto;
  max-height: 120px;
  margin-top: 10px;
  color: #999999;
  line-height: 30px;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 4;
  overflow: hidden;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}
li:hover .imgBox img {
  transform: scale(1.1);
}
</style>



